import React, { useState } from 'react';

function Country() {
 let countriesSha;
  const [count, setCount] =  useState([])
  const [search, setSearch] = useState('')
  const [searchRes, setSearchRes] = useState('')
  const [searchByRegion, setSearchByRegion] = useState('')
    function callMe(){
 fetch(`https://restcountries.eu/rest/v2/all`)
     .then(res => res.json())
     .then(result => {
        
          setCount(result) }) 
    }
 
   const see =  count.map( coun=> {
        return (
            < Coun key={coun.name} country={coun.name} population={coun.population} 
             flag={coun.flag} region={coun.region}  capital={coun.capital}/> 
        )
    })

const goTo = evt =>{
     if(evt.key === 'Enter'){
       fetch(`https://restcountries.eu/rest/v2/name/${search}`)
       .then( res => res.json())
       .then(resoltt => {
          setSearchRes(resoltt)
             setCount([])
       })
       .catch(err => 'Something went wrong')
   }
}
function regionHandle (){
  fetch(`https://restcountries.eu/rest/v2/region/${searchByRegion}`)
.then(res => res.json())
.then(result => {
     setSearchByRegion(result)
     if(typeof searchByRegion !== 'string'){
     countriesSha =  searchByRegion
    }
    }).catch(err=> err)

}



        return (
          <section>
            <button  className="funny" onClick={callMe}>View All Nations</button>
               <input  className="funny" name='search' 
               placeholder="search by Country" 
               value={search} 
               onChange={e=> setSearch(e.target.value)}
               onKeyPress={goTo}
               />
               <select value={searchByRegion} onChange={e=> {
                setSearchByRegion(e.target.value)
          
              }} onClick={regionHandle}>
               <option value="">Filter By Region</option>
               <option value=''>Africa</option>
              <option value="" >Europe</option>
               <option value="">Americas</option>
                 <option value="">Asia</option>
                 <option value="">Oceania</option>
               </select>
             <div className="funny-bone">
                {see}
               
              </div>
              < Controller names={searchRes} />
           
            
            </section>    
        )}

function Coun(props){

    return (
       
    <div>
        <div>
           <div >
           <img src={props.flag}/>
    <p>Country : {props.country}</p>
    <p>Population :{props.population}</p>
    <p>{props.region}</p>
    <p>{props.capital}</p>
           </div>
        </div>
    
    </div>
      
    
    )
}
function Controller(props){

  return (
    
     <div>
        {(typeof props.names[0] != 'undefined') ? (
        <div>
        <img src={props.names[0].flag}/>
        <h3>Name : {props.names[0].name}</h3>
        <p>Population: {props.names[0].population}</p>
        <p> Region : {props.names[0].region}</p>
         <p> Capital : {props.names[0].capital}</p>
       </div> 
         ) : ('')}  
         { <p style={{display : props.names.status > 400 ? 'block' : 'none'}} >Country Not Found</p>}
       </div>
  )
}










export default Country

 

